<template>
	<div class="page">
        
        <div v-if="mode == 'submit'">
            <topsubmenu :toptitle="toptitle"></topsubmenu>
            <div class="address-box"  v-if="addressList.length != 0">
                <div class="address" @click="showMode('address')"> 
                    <span class="location-icon"></span>
                    <div class="addr-text">
                        <div class="per-info"><span>收货人：{{defaultAddress.name}}</span> <span class="per-tel">{{defaultAddress.phone}}</span> </div>
                        <div class="address-detail">{{defaultAddress.addressArea + " "+ defaultAddress.areaDetail}}</div>
                    </div> 
                    <span class="right-arrow" style="visibility: visible;"></span> 
                </div>
            </div>
            <div class="no-address" v-if="addressList.length == 0">
                <div class="no-addr-icon"></div> 
                <div class="no-addr-text">
                    还没有收货地址，<a href="javascript:;" @click="showMode('address')">去添加</a>
                </div> 
            </div>
            <div class="time-select-box">
                <span class="calendar-icon"></span>
                <span class="time-text">仅工作日收货</span>
                <span class="right-arrow" style="visibility: visible;"></span>
            </div>

            <div class="order-groups">
                <div class="order-group">
                    <!-- <div class="group-head"> 
          					<span class="store-icon"></span> 
          					<span class="store-info">聚美优品发货</span> 
        				</div>  -->
                    <div class="product-items" v-for="item in productlist">
                        <div class="product-item">
                            <div class="item-img">
                                <img :src="$store.state.domainUrl + item.src">
                            </div>
                            <div class="item-info">
                                <div class="item-info-1">
                                    <span class="item-title"> {{item.name}}</span>
                                    <span class="item-price">¥{{item.price}}</span>
                                </div>
                                <div class="item-info-2">
                                    <span>{{item.sku}}</span>
                                </div>
                                <div class="item-info-2">
                                    <span></span>
                                    <span>x{{item.count}}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="group-stat">
                        <div class="stat-nav">
                            <div>运费</div>
                            <div class="freight-info">
                                <div class="fre-info-1">快递：¥0</div>
                                <div class="fre-info-2">全场包邮</div>
                            </div>
                        </div>
                        <div class="stat-nav">
                            <div class="nav-center">
                                <span>现金券</span>
                                <span class="no-select-tag" v-if="couponCount == 0">暂无可用</span>
                                <span class="sel-promo" v-if="couponCount > 0">{{couponCount}}</span>
                            </div>
                            <div>
                                <span v-if="couponCode == ''">使用现金券</span>
                                <span v-if="couponCode != ''">已抵扣{{couponPrice}}元</span>
                                <span class="right-arrow-icon"></span>
                            </div>
                        </div>
                        <!-- <div class="stat-nav"> 
        						<div class="nav-center"> 
        							<span>红包</span>  
        							<span class="no-select-tag">暂无可用</span> 
        						</div> 
        						<div> 
        							<span>使用红包<span class="right-arrow-icon"></span>
        							</span>   
        						</div> 
        					</div>  -->
                        <div class="stat-nav">
                            <div class="nav-center">小计 </div>
                            <div class="tr">
                                <div class="notice-num">¥{{parseFloat(total).toFixed(2)}}</div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="submit-box">
                <div class="order-stat">
                    <div class="stat-sum">合计 <span>¥{{parseFloat(total).toFixed(2)}}</span>
                    </div>
                    <div class="stat-count">共 {{count}} 件商品</div>
                </div>
                <button type="button">提交订单</button>
            </div>
        </div>

        <div v-if="mode == 'address'">
            <header class="touch-header">
                <a href="javascript:void(-1);" @click="addressBack" id="touch-header-back" class="touch-header-back">
                  </a>
                <div class="touch-header-title">
                    地址
                </div>
            </header>
            <div v-if="addressMode == 'edit'" style="background: #fff;padding: 0 .75rem;">
                <div class="form-group">
                    <label>收货人</label>
                    <input type="text" placeholder="请输入收货人的姓名" :value="editAddress.name"> 
                </div>
                <div class="form-group">
                    <label>手机号码</label>
                    <input type="tel" placeholder="请输入收货人的手机号码" maxlength="11" :value="editAddress.phone">
                </div>
                <div class="form-group">
                    <label>省市区</label>
                    <input type="text" @click="isLoadedArea = true;" readonly="readonly">
                    <span class="location">定位</span>
                </div> 
                <div class="form-group detail-addr">
                    <label>详细地址</label>
                    <textarea placeholder="请输入收货人的详细地址"></textarea>
                    <span class="clear"></span>
                </div>
                <div class="btns">
                    <button type="button" class="save">保存地址</button>
                    <button type="button" class="del" v-if="editAddress.id > 0">删除地址</button>
                </div>

                <div class="over-lay" v-if="isLoadedArea"> 
                    <div class="address-picker"> 
                        <div class="address-title">
                            <a href="javascript:;">取消</a> <a href="javascript:;">确认</a>
                        </div>
                        <div class="address-content">
                            <div class="scroller">
                                <scroller :snapping="true" :snap-height="50" ref="my_scroller">
                                    <ul  @touchend="touchend">
                                        <li 
                                        :class="{'selected': item.isSelected}" 
                                        :data-code="item.id"
                                        @click="selectProvince(item)"
                                        v-for="item in regionList">
                                            {{item.name}}
                                        </li>
                                    </ul>
                                </scroller>
                            </div>
                            <div class="scroller">
                                <scroller :snapping="true" :snap-height="50">
                                    <ul>
                                        <li 
                                        :class="{'selected': item.isSelected}" 
                                        :data-code="item.id" 
                                        @click="selectCity(item)"
                                        v-for="item in cityList">
                                            {{item.name}}
                                        </li>
                                    </ul>
                                </scroller>
                            </div>
                            <div class="scroller">
                                <scroller :snapping="true" :snap-height="50">
                                    <ul>
                                        <li :class="{'selected': item.isSelected}" :data-code="item.id" v-for="item in districtList">{{item.name}}</li>
                                    </ul>
                                </scroller>
                            </div>
                            <div class="highlight"></div> 
                        </div> 
                    </div> 
                </div>
            </div>
            <div v-if="addressMode == 'list'">
                <div class="items"> 
                    <div class="item-outer" v-for='item in addressList'> 
                        <div class="item"> 
                            <div class="left" @click="setDefault(item)"> 
                                <span :class="{'icon-checked':item.isDefault, 'icon-unchecked': !item.isDefault}"></span>
                            </div>
                            <div class="info">
                                <div class="name-and-phone">
                                    <div class="name">{{item.name}}</div>
                                    <div class="phone">{{item.phone}}</div>
                                </div>
                                <div class="address">{{item.addressArea + item.areaDetail}}</div>
                            </div>
                            <div class="right" @click="editAddressPage(item)"> <span class="icon-edit"></span> </div>
                        </div>
                    </div>
                </div>

                <div class="tips">最多保存10个有效地址。每月只能新增或修改10次。您本月已新增或修改3次</div>
                <div class="addressFooter">
                    <button type="button" @click="addAddressPage">新增地址</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script type="text/javascript">
	export default{
		name:"submitorder",
        components: {
            topsubmenu: require('../components/topmenu.vue')
        },
		data(){
			return{
                //模式
                mode:"submit",
                //商品总额
				total:0,
                //商品列表
				productlist:[],
                //商品数量
				count:0,
                //商品sku
				itemid: '',
                //标题
                toptitle:"结算中心",
                //可用优惠券数量
                couponCount:0,
                //使用的优惠券代码
                couponCode:"",
                //使用优惠券金额
                couponPrice:0,
                //地址列表
                addressMode:"list",
                //编辑模式
                editAddress:{
                },
                //可用地址列表
                addressList:[],
                //默认地址
                defaultAddress:{
                },
                //地区选择栏是否初始化
                isLoadedArea:false,
                regionList:[],
                cityList:[],
                districtList:[]
			}
		},
		mounted(){
			this.$store.commit('changeMenu', false);
        	this.$store.commit('changeNav', "submitorder");
        	this.$store.commit('changeTitle', this.toptitle);

            //init shoppingcart data
            for (var i = this.$store.state.cartlist.length - 1; i >= 0; i--) {
                if(this.$store.state.cartlist[i].check == true) {
                    this.productlist.push(this.$store.state.cartlist[i]);
                    this.total += parseFloat(this.$store.state.cartlist[i].price * this.$store.state.cartlist[i].count);
                    this.count += this.$store.state.cartlist[i].count;
                }
            }

            //初始化
            this.init();
		},
		methods: {

	        init() {
                var skulist = [];
	        	if (this.itemid === '') {
	        		//this.productlist = this.$store.state.cartlist;
                    for (var i = this.productlist.length - 1; i >= 0; i--) {
                        skulist.push({SKU:this.productlist[i].id, number:this.productlist[i].count})
                    }
	        	} else {
                    skulist.push({SKU:this.productlist[i].id, number:this.productlist[i].count})
	        	}

	        	this.$http.post(this.$store.state.apidomain + "m/app/InitSubmitOrder", {
	        		userid: this.$store.state.auth.id,
	        		productList: JSON.stringify(skulist)
	        	}).then(function(response) {
                    this.couponCount = response.body.result.couponCount;
	        	}, function(response) {
	        		console.info(response);
	        	})

                this.initAddress();
	        },
            //初始化地址
            initAddress() {
                this.$http.post(this.$store.state.apidomain + "m/app/AddressList", {
                    userid: this.$store.state.auth.id,
                    pageIndex:1,
                    pageNum:10
                }).then(function(response) {
                    if (response.body.result == null) {
                        this.addressMode = "edit";
                        return;
                    }

                    this.addressList = response.body.result;

                    this.defaultAddress = this.addressList[0];

                    for (var i = this.addressList.length - 1; i >= 0; i--) {
                        if(this.addressList[i].isDefault){
                            this.defaultAddress = this.addressList[i];
                        }
                    }
                }, function(response) {
                    console.info(response);
                })

                this.initRegionList();
            },
            //切换页面
            showMode(id) {
                this.mode = id;
            },


            //地址编辑返回
            addressBack() {
                if (this.addressMode == 'edit')
                    this.addressMode = 'list';
                else
                    this.mode = 'submit';
            },
            //设置默认地址
            setDefault(item) {
                this.defaultAddress = item;
                this.mode = 'submit';
            },
            //编辑地址
            editAddressPage(item){
                this.editAddress = item;
                this.addressMode = 'edit';
            },
            SaveAddress() {
                this.$http.post(this.$store.state.apidomain + "m/app/SaveAddress",{
                    id:this.editAddress.id, 
                    UserId:this.$store.state.auth.id, 
                    name:this.editAddress.name, 
                    phonenumber:this.editAddress.phone, 
                    areaid: 0,
                    areadetail:this.editAddress.areaDetail, 
                    zipcode:this.editAddress.zipcode, 
                    celphone:'',
                    isDefault:true
                }).
                then(function(response){

                }, function(response){

                })
            },
            //添加地址
            addAddressPage() {
                this.editAddress = {};
                this.addressMode = 'edit';
            },
            //删除地址
            delAddress(id) {

            },
            //地区选择
            initRegionList(){
                this.$http.post(this.$store.state.apidomain + "m/app/GetRegionList",{
                    regionId: 0
                })
                .then(function(response) {

                    this.regionList = response.body.result;
                    this.regionList[0].isSelected = true;
                    this.selectProvince(this.regionList[0]);
                    this.selectCity(this.cityList[0]);

                }, function(response) {
                    console.info(response);
                })
            },
            touchend(){
                console.info(this.$refs.my_scroller.getPosition())
                this.$refs.my_scroller.scrollBy(0,50, null);
            },
            selectProvince(item){
                this.cityList = item.childlist;
                this.cityList[0].isSelected = true;
            },
            selectCity(item){
                this.districtList = item.childlist;
                this.districtList[0].isSelected = true;
            }
		}
	}
</script>
