    -ms-flex-align: center;
    align-items: center;
}.item .info .name-and-phone .name {
    width: 8.4375rem;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}.item .info .name-and-phone .phone {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    text-align: right;
}.item .info .address, .item .info .id-card {
    color: #777;
}.item .right {
    -webkit-box-pack: end;
    -ms-flex-pack: end;
    justify-content: flex-end;
}.icon-edit {
    width: 1.25rem;
    height: 1.25rem;
    background-image: url(data:image/png;base64,);
    background-size: 100% 100%;
    display: block;
}.tips {
    padding: 0 .875rem 3.84375rem 1.90625rem;
    color: #999;
    font-size: .6875rem;
    position: relative;
}.tips::before {
    position: absolute;
    top: .25em;
    left: .78125rem;
    content: "";
    width: .78125rem;
    height: .78125rem;
    background-image: url(data:image/png;base64,);
    background-size: 100% 100%;
}
.addressFooter{
        position: fixed;
    right: 0;
    bottom: 0;
    left: 0;
    height: 3.0625rem;
    background: #fff;
    border-top: 1px solid #eee;
    z-index: 1000;
}.addressFooter button {
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -1.125rem 0 0 -6.25rem;
    width: 12.5rem;
    height: 2.25rem;
    font: inherit;
    color: #fff;
    font-size: .875rem;
    background: #fe4070;
    border: 0;
    border-radius: 1.125rem;
}
.icon-unchecked {
    width: 1.25rem;
    height: 1.25rem;
    background-image: url(data:image/png;base64,);
    background-size: 100% 100%;
    display: block;
}

.form-group {
    font-size: .675rem;
    border-bottom: 1px solid #eee;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}.form-group label {
    color: #999;
    line-height: 2.75rem;
    display: block;
    -webkit-box-flex: 0;
    -ms-flex: 0 0 5.5625rem;
    flex: 0 0 5.5625rem;
}.form-group input, .form-group textarea {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    box-sizing: border-box;
    height: 2.75rem;
    padding: .71875rem 0;
    border: 0;
    outline: 0;
    font: inherit;
    display: block;
}.form-group:last-child {
    border-bottom: 0;
}.form-group .clear {
    width: 2.75rem;
    height: 2.75rem;
    background: url(data:image/png;base64,) no-repeat center;
    background-size: .875rem .875rem;
}
/*地区选择框*/
.over-lay {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0,0,0,.5);
    z-index: 1000;
}.address-picker {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
}.address-picker .address-title {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 2.75rem;
    padding: 0 1rem;
    font-size: .875rem;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    background: #f5f5f5;
}.address-picker .address-title a {
    color: #fe4070;
    text-decoration: none;
    display: block;
}.address-picker .address-content {
    height: 9.5625rem;
    background: #fff;
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
}.address-picker .address-content .scroller {
    overflow: hidden;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
}.address-picker .address-content .scroller ul {
    padding: 3.53125rem 0;
}.address-picker .address-content .scroller ul li {
    text-align: center;
    line-height: 50px;
    color: #999;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}.address-picker .address-content .scroller ul li.selected {
    color: #333;
    font-size: .875rem;
}.address-picker .address-content .highlight {
    height: 50px;
    box-sizing: border-box;
    border-bottom: 1px solid #eee;
    border-top: 1px solid #eee;
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    margin-top: -1.25rem;
    pointer-events: none;
}
</style>


