<template>
  <div class="page bg-content">
    <topsubmenu :toptitle="toptitle"></topsubmenu>

    <div class="cart-no-goods" v-if="$store.state.cartcount == 0">
        <div class="oops"></div>
        <div class="empty-cart">结算时间结束后，商品可能被抢空，请尽快结算</div>
        <a class="go-stroll" href="/#/">去逛逛</a>
    </div>

    <div v-if="$store.state.cartcount > 0">
        <div class="count-down">
            <div class="clock-msg">商品数量有限，请尽快结算</div>
        </div>

        <div class="groups">
            <div>
                <div class="group">
                    <div class="group-content">
                        <div>
                            <div class="item" v-for="item in $store.state.cartlist">
                                <span :class="{'check-box-un-checked': !item.check, 'check-box-checked': item.check}" @click="checkboxclick(item.id, item.check)">
                                </span>
                                <div class="item-content">
                                    <div class="img-title">
                                        <div class="img-wrap">
                                            <img :src="$store.state.domainUrl + item.src" :alt="item.name">
                                        </div>
                                        <div class="text-wrap" v-if="editids.indexOf(item.id) < 0">
                                            <span class="title"> {{item.name}} </span>
                                            <div class="sub-title-1"> <span>{{item.sku}}</span> <span>×{{item.count}}</span> </div>
                                            <div class="price-edit"> <span class="price"> ¥{{(item.price * item.count).toFixed(2)}} </span>
                                                <span class="edit" @click='edit(item.id)'>编辑</span> </div>
                                        </div>
                                        <div class="text-wrap" v-else-if="editids.indexOf(item.id) >= 0">
                                            <span class="title">
                                                {{item.name}}
                                            </span>
                                            <div class="number-editor">
                                                <span class="decrease" @click="decrease(item);"></span>
                                                <span>{{item.count}}</span>
                                                <span class="increase" @click="item.count++;"></span>
                                            </div>
                                            <div class="price-edit">
                                                <span class="price editing"> ¥{{(item.count * item.price).toFixed(2)}} </span>
                                                <div class="actions">
                                                    <span class="delete" @click="deletecart(item.id)">删除</span>
                                                    <span class="gap-line"></span>
                                                    <span class="action-finish" @click="update(item)">完成</span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>

    <div class="submit-bottom submit-bottom-padding">
        <div class="sub-info">
            <span :class="{'check-box-checked': $store.state.cartlist.length == checklist.length, 'check-box-un-checked': $store.state.cartlist.length != checklist.length}" @click="checkAll"></span>
            <span class="all-check-text">全选</span>
            <div class="summary">
                <div>
                    <span>合计</span>
                    <span class="red">¥{{parseFloat(carttotal).toFixed(2)}}</span>
                </div>
            </div>
        </div>
        <div class="submit-btn go-to-submit" @click="submit"> 去结算<span>({{selectedcartcount}})</span> </div>
    </div>

</div>
</template>

<script>
export default {
	name: "shoppingcart",
    components: {
        topsubmenu: require('../components/topmenu.vue')
    },
	data(){
		return {
            editids: [],
            checklist: [],
            carttotal: 0,
            selectedcartcount: 0,
            toptitle:"购物车"
		}
	},
	mounted(){
        this.$store.commit('changeMenu', false);
        this.$store.commit('changeNav', "cart");
        this.$store.commit('changeTitle', this.toptitle);

        //init shoppingcart data
        for (var i = this.$store.state.cartlist.length - 1; i >= 0; i--) {
            if(this.$store.state.cartlist[i].check == true){
                this.checklist.push(this.$store.state.cartlist[i].id);
                this.carttotal += parseFloat(this.$store.state.cartlist[i].price * this.$store.state.cartlist[i].count);
                this.selectedcartcount += this.$store.state.cartlist[i].count;
            }
        }
	},
	methods: {
        //decrease button
        decrease(item){
            if (item.count == 1) {
                this.deletecart(item.id)
            } else {
                item.count--;
            }
        },
        //eidt button
        edit(id){
            this.editids.push(id);
        }, 
        //select all
        checkAll() {
            this.carttotal = 0;this.selectedcartcount = 0;
            //某项被选中,先清空
            if (this.checklist.length > 0) {
                this.checklist = [];
                for (var i = this.$store.state.cartlist.length - 1; i >= 0; i--) {
                    this.$store.state.cartlist[i].check = false;
                }
            //没有选中,做全选
            } else {
                for (var i = this.$store.state.cartlist.length - 1; i >= 0; i--) {
                    this.$store.state.cartlist[i].check = true;
                    this.checklist.push(this.$store.state.cartlist[i].id);
                    this.selectedcartcount += this.$store.state.cartlist[i].count;
                    this.carttotal += parseFloat(this.$store.state.cartlist[i].price) * parseFloat(this.$store.state.cartlist[i].count);
                }
            }

            this.$store.commit("SyncLocalStorageCart", null);
        },
        //select one
        checkboxclick(id, check){
            if (check) {
                this.checklist.splice(this.checklist.indexOf(id), 1);
            } else {
                this.checklist.push(id);
            }
            this.carttotal = 0;
            this.selectedcartcount = 0;
            for (var i = this.$store.state.cartlist.length - 1; i >= 0; i--) {
                if(this.$store.state.cartlist[i].id == id){
                    this.$store.state.cartlist[i].check = !check;
                }
                if (this.$store.state.cartlist[i].check){
                    this.selectedcartcount += this.$store.state.cartlist[i].count;
                    this.carttotal += parseFloat(this.$store.state.cartlist[i].price) * parseFloat(this.$store.state.cartlist[i].count);
                }
            }

            this.$store.commit("SyncLocalStorageCart", null);
        },
        //del item
        deletecart(id){
            this.$confirm('确定要删除此商品？', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
                this.$store.commit("delCart", id);
                for (var i = this.editids.length - 1; i >= 0; i--) {
                    if(this.editids[i] == id){
                      //state.cartlist[i].count = parseInt(data.count);
                      this.editids.splice(i, 1);
                      break;
                    }
                }
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
                this.carttotal = 0;
                this.selectedcartcount;
                for (var i = this.$store.state.cartlist.length - 1; i >= 0; i--) {
                    if (this.$store.state.cartlist[i].check){
                        this.selectedcartcount += this.$store.state.cartlist[i].count;
                        this.carttotal += parseFloat(this.$store.state.cartlist[i].price) * parseFloat(this.$store.state.cartlist[i].count);
                    }
                }
            }).catch(() => {
              this.$message({
                type: 'info',
                message: '已取消删除'
              });
            });
        },
        //update cart
        update(item){
            this.$store.commit("updateCart", item);
            this.carttotal = 0;
            this.selectedcartcount = 0;
            for (var i = this.$store.state.cartlist.length - 1; i >= 0; i--) { 
                if (this.$store.state.cartlist[i].check){
                    this.selectedcartcount += this.$store.state.cartlist[i].count;
                    this.carttotal += parseFloat(this.$store.state.cartlist[i].price) * parseFloat(this.$store.state.cartlist[i].count);
                }
            }
            console.info(this.carttotal)
            for (var i = this.editids.length - 1; i >= 0; i--) {
                if(this.editids[i] == item.id){
                  //state.cartlist[i].count = parseInt(data.count);
                  this.editids.splice(i, 1);
                  break;
                }
            }
        },
        //
        submit(){
            if (this.checklist.length == 0) {
                this.$message.error("请选择要结算的商品");
                return;
            }

            //TODO:check

            this.$router.push({path:'/submit'})
        }
	}
}
</script>
<style>
  .cart-page footer{
    display: none;
  }
.page {
        padding-bottom: 6.4375rem;
    }
  /*no goods*/
  .cart-no-goods {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    background: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}.cart-no-goods .oops {
    width: 5.375rem;
    height: 5.125rem;
    background: url(../../assets/imgs/shoppingcart/no-card-oops.png) no-repeat;
    background-size: 100% 100%;
}.cart-no-goods .empty-cart {
    font-size: .875rem;
    line-height: .9375rem;
    color: #333;
    margin-top: 1rem;
    padding: 0 .75rem;
    text-align: center;
}.cart-no-goods .go-stroll {
    width: 7.8125rem;
    height: 2.25rem;
    border-radius: 1.25rem;
    color: #fff;
    background: #fe4070;
    line-height: 2.25rem;
    text-align: center;
    margin-top: 1.4375rem;
    font-size: .8125rem;
}


/*结算栏*/
html * {
    outline: 0;
    -webkit-text-size-adjust: none;
    -webkit-tap-highlight-color: transparent;
}
.submit-bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    box-shadow: 0 0 0.15625rem #eee;
    height: 3.0625rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    background: #fff;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    padding: 0 .75rem 0 .5rem;
    z-index: 1;
}.submit-bottom.submit-bottom-padding {
    bottom: 3.0625rem;
}.submit-bottom .sub-info {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}.check-box-checked, .groups .group .group-content .item .check-box-checked, .submit-bottom .sub-info .check-box-checked {
    display: block;
    background: url(data:image/png;base64,) no-repeat;
    background-size: 100% 100%;
    width: 1.25rem;
    height: 1.25rem;
}.check-box-un-checked, .groups .group .group-content .item .check-box-un-checked, .submit-bottom .sub-info .check-box-un-checked {
    display: block;
    background: url(data:image/png;base64,) no-repeat;
    background-size: 100% 100%;
    width: 1.25rem;
    height: 1.25rem;
}.groups .group .group-content .item .check-box-un-checked {
    margin-top: 2.8125rem;
}
.submit-bottom .sub-info .all-check-text {
    font-size: .875rem;
    color: #333;
    margin-left: .75rem;
}
.submit-bottom .sub-info .summary {
    margin-left: .6875rem;
    color: #999;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
}.submit-bottom .sub-info .summary>div:nth-child(1) {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}.submit-bottom .sub-info .summary>div .red {
    font-size: 1rem;
    margin-left: .3125rem;
}
.submit-bottom .submit-btn {
    width: 7.8125rem;
    height: 2.25rem;
    font-size: .875rem;
    color: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    border-radius: 1.25rem;
}.submit-bottom .go-to-submit {
    background: #fe4070;
}

/*内容*/
.count-down {
    padding: 0 .5rem;
    height: 1.875rem;
    color: #999;
    background: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}.count-down .clock-msg {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}.groups {
    margin-top: .5rem;
}.groups .group {
    margin-bottom: .5rem;
    background: #fff;
}.groups .group .group-header {
    background: #fff;
    height: 2.5rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding-left: .5rem;
    border-bottom: 1px #eee solid;
}.groups .group .group-header .group-title {
    margin-left: .5rem;
}.groups .group .group-content .item {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    padding: 0 .75rem 0 .5rem;
}.groups .group .group-content .item .check-box-checked {
    margin-top: 2.8125rem;
}.groups .group .group-content .item .item-content {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
    width: 100%;
}.groups .group .group-content .item .item-content .img-title {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-left: .5rem;
    padding-bottom: .625rem;
}.groups .group .group-content .item .item-content .img-title {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-left: .5rem;
    padding-bottom: .625rem;
}.groups .group .group-content .item .item-content .img-title .img-wrap {
    width: 5.25rem;
    height: 5.25rem;
    position: relative;
    margin-top: .625rem;
}.groups .group .group-content .item .item-content .img-title .img-wrap img {
    width: 100%;
    height: 100%;
}.text-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    margin-left: .625rem;
    margin-top: .5rem;
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
}.text-wrap .title {
    display: -webkit-box;
    width: 10.5625rem;
    max-height: 2.5rem;
    text-overflow: ellipsis;
    overflow: hidden;
    color: #333;
    line-height: 1.25rem;
    text-align: left;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    word-break: break-all;
    white-space: nowrap;
}.text-wrap .sub-title-1 {
    margin-top: .1875rem;
    line-height: .75rem;
    height: .75rem;
    color: #999;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
}.text-wrap .sub-title-1 span:first-child {
    display: block;
    max-width: 9.1875rem;
    font-size: .6875rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}.text-wrap .price-edit {
    margin-top: .3125rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
    -ms-flex-pack: justify;
    justify-content: space-between;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    height: 1.375rem;
}.text-wrap .price-edit .price {
    width: 9.5625rem;
    display: block;
    font-size: .9375rem;
    color: #fe4070;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}.text-wrap .price-edit .edit {
    display: block;
}
.text-wrap .number-editor {
    margin-top: .5rem;
    height: 1.4375rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}.text-wrap .number-editor .decrease {
    display: block;
    width: 1.375rem;
    height: 1.40625rem;
    background: url(data:image/png;base64,) no-repeat;
    background-size: 100% 100%;
}
.text-wrap .number-editor span:nth-child(2) {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    width: 2.5rem;
    height: 1.375rem;
    color: #333;
}.text-wrap .number-editor .increase {
    display: block;
    width: 1.375rem;
    height: 1.40625rem;
    background: url(data:image/png;base64,) no-repeat;
    background-size: 100% 100%;
}
.text-wrap .price-edit .price.editing {
    width: 7.21875rem;
}.text-wrap .price-edit .actions {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}.text-wrap .price-edit .actions .gap-line {
    display: inline-block;
    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAMAAABLCAYAAAC1Iz2+AAAAAXNSR0IArs4c6QAAACFJREFUKBVjfPz48X8GKGCCMUD0KGc0DEbTASRHDJm8AAAF3wQ+7br5wgAAAABJRU5ErkJggg==) no-repeat;
    width: 1px;
    height: .78125rem;
    background-size: 100% 100%;
    margin-left: .375rem;
    margin-right: .375rem;
}
</style>



