<template>
	<header class="touch-header">
        <a href="javascript:void(-1);" @click="goback" id="touch-header-back" class="touch-header-back">
          </a>
        <div class="touch-header-title">
            {{toptitle}}
        </div>
        <!--<a class="touch-header-home" href="/#/">
        </a>-->
        <a class="touch-header-home" @click="showTopSubMenu">
        </a>
        <!--导航-->
        <ul class="nav-more-list" id="sub-title" v-if="showMenu" style="transform-origin: 0px 0px 0px; opacity: 1; transform: scale(1, 1);">
            <li>
                <a class="nav-more-icon home-icon" href="/#/">首页</a>
            </li>
            <li>
                <a class="nav-more-icon cart-icon" href="/#/cart">购物车</a>
            </li>
            <li>
                <a class="nav-more-icon search-icon" href="/#/search">搜索</a>
            </li>
            <li>
                <a class="nav-more-icon cate-icon" href="/#/list">全部分类</a>
            </li>
            <li>
                <a class="nav-more-icon ebuy-icon" href="/#/my">我的故事</a>
            </li>
        </ul>
    </header>
    
</template>

<script type="text/javascript">
	export default{
        props: {
            toptitle: ""
        },
		data(){
			return{
				showMenu: false
			}
		},
		methods:{
			//go back
            goback(){
                window.history.go(-1)
            },
            showTopSubMenu(){
                console.info(1)
                this.showMenu = !this.showMenu;
            }
		}
	}
</script>
<style>
.nav-more-list {
    position: absolute;
    top: 2.36rem;
    right: 0.3rem;
    background: #484F55;
    z-index: 112;
    border: 1px solid transparent;
    border-radius: 2px;
}.nav-more-list li {
    border-bottom: 1px solid #5B6166;
    width: 5rem;
    height: 1.8rem;
    line-height: 1.8rem;
    text-align: left;
    text-indent: 2.7em;
    background: #484F55;
}.nav-more-list li a {
    position: relative;
    display: block;
    width: 100%;
    color: #fff;
}.nav-more-list:after {
    content: "";
    position: absolute;
    top: -.9rem;
    right: .2rem;
    display: inline-block;
    width: 0;
    height: 0;
    border-style: solid;
    border-color: transparent transparent #484F55;
    border-width: .5rem;
}
.nav-more-list li a.nav-more-icon:after {
    content: "";
    position: absolute;
    left: .3rem;
    top: 50%;
    margin-top: -.5rem;
    width: 1rem;
    height: 1rem;
    -webkit-mask-repeat: no-repeat;
    background-image: -webkit-gradient(linear,50% 0,50% 100%,color-stop(0,#fff),color-stop(100%,#fff));
    -webkit-mask-size: 1rem;
}

.nav-more-list li a.nav-more-icon.home-icon:after, .sn-ibar-wrap .item:nth-child(1) .icon {
    -webkit-mask-image: url(data:image/png;charset=utf-8;base64,);
    -webkit-mask-position: 0 0;
}
.my-cart .icon, .nav-more-list li a.nav-more-icon.cart-icon:after, .sn-ibar-wrap .item:nth-child(2) .icon {
    -webkit-mask-image: url(data:image/png;charset=utf-8;base64,);
    -webkit-mask-position: 0 0;
}.nav-more-list li a.nav-more-icon.search-icon:after, .sn-ibar-wrap .item:nth-child(3) .icon {
    -webkit-mask-image: url(data:image/png;charset=utf-8;base64,);
    -webkit-mask-position: 0 0;
}.nav-more-list li a.nav-more-icon.cate-icon:after, .sn-ibar-wrap .item:nth-child(4) .icon {
    -webkit-mask-image: url(data:image/png;charset=utf-8;base64,);
    -webkit-mask-position: 0 0;
}
.nav-more-list li a.nav-more-icon.ebuy-icon:after, .sn-ibar-wrap .item:nth-child(5) .icon {
    -webkit-mask-image: url(data:image/png;charset=utf-8;base64,);
    -webkit-mask-position: 0 0;
}
</style>


